// Small devices (landscape phones, 576px and up)
@media (min-width: 280px) and (max-width: 768px) {
    p {
        text-align: initial;
    }

    header.navigation {
        nav {
            display: none;
        }

        .logo {
            height: rem(18px);
            position: relative;
            top: 0px;
        }
    }

    h6 {
        font-size: rem(16px);
    }

    h5 {
        font-size: rem(18px);
    }

    h4 {
        font-size: rem(20px);
    }

    h3 {
        font-size: rem(22px);
    }

    h2 {
        font-size: rem(24px);
    }

    h1 {
        font-size: rem(26px);
    }

    h1,
    h2,
    h3,
    h4,
    h5,
    h6 {
        color: var(--default-heading-color);
    }

    .theme-switch {
        position: absolute;
        right: rem(20px);
        top: rem(22px);
        padding: 0;
    }

    .lined-heading {
        font-size: rem(25px);
        margin: rem(10px);
    }

    main.app-container main {
        margin-top: 0px;

        .posts {
            .post {
                span.date {
                    margin: rem(15px) 0 rem(5px) 0;
                    display: block;
                }

                .title {
                    font-size: rem(18px);
                }

                .excerpt {
                    font-size: rem(14px);
                }

                .tags {
                    .tag {
                        background-color: transparent;
                        padding: rem(1px) rem(12px) rem(3px) rem(12px);
                        font-size: rem(12px);
                        color: var(--normal-text-color);
                        border-color: var(--tags-border-color);
                    }
                }

                .thumbnail {
                    margin-top: 0;
                }
            }
        }

        nav.pagination a {
            padding: rem(5px) rem(20px);
            font-size: rem(14px);
        }

        aside.toc {
            display: none;
        }

        .hero-tags a.tag {
            margin: rem(5px) rem(10px);
        }

        .hero-post .hero-title {
            font-size: rem(30px);
        }

        main.app-container {
            margin-top: rem(5px);
        }

        main section.posts article.post .thumbnail {
            margin-top: 0;
        }

        div.profile {
            margin: initial;
        }
    }

    p {
        font-size: rem(16px);
    }
}

