@import url('https://fonts.googleapis.com/css2?family=Mulish:wght@200;400;600;700&display=swap');

$font-size: 14px;

@import 'bootstrap-reboot';
@import 'bootstrap-grid';

@import 'helper';
@import 'syntax-highlight';

* {
  transition: all 0.2s;
}

:root {
  --brand-color: #55cbff;
  --normal-text-color: #282c35;
  --background-color: #ffffff;
  --logo: url(/assets/images/logo-light.svg);
  --nav-link-color: #39455B;
  --nav-link-hover-color: #222222;
  --post-title-color: #282c35;
  --post-date-color: #aaaaaa;
  --post-excerpt-color: #282c35;
  --post-featured-color: #6c5ce7;
  --theme-switch-font-color: #282c35;
  --paragraph-font-color: #282c35;
  --lined-heading-color: #282c35;
  --header-border-bottom-color: #eeeeee;
  --header-border-top-color: #222222;
  --anchor-tag-font-color: #282c35;
  --anchor-tag-font-hover-color: #888888;
  --footer-text-color: #222;
  --terms-font-color: #282c35;
  --nav-link-before-hover-color: #282c35;
  --nav-link-before-color: #282c35;
  --hero-tag-color: #282c35;
  --footer-background-color: #000000;
  --default-heading-color: #282c35;
  --unordered-list-color: #282c35;
  --tags-border-color: #222222;
  --pagination-button-default-bg: #222222;
  --pagination-button-hover-bg: #ffffff;
  --pagination-button-default-font-color: #ffffff;
  --pagination-button-hover-font-color: #222222;
  --notfound-frown-face-color: #222222;
  --syntax-highlight-bg-color: rgba(0, 0, 0, 0.1);
  --blockquote-background-bg-color: #f8f9fa;
  --blockquote-font-color: #495057;
  --blockquote-border-color: #495057;
}

[data-theme="dark"] {
  --brand-color: #55cbff;
  --normal-text-color: rgba(255, 255, 255, 0.90);
  --background-color: #1B2430;
  --logo: url(/assets/images/logo-dark.svg);
  --nav-link-color: rgba(255, 255, 255, 0.90);
  --nav-link-hover-color: rgba(255, 255, 255, 0.90);
  --post-title-color: rgba(255, 255, 255, 0.90);
  --post-date-color: #dddddd;
  --post-excerpt-color: #eeeeee;
  --post-featured-color: #6c5ce7;
  --theme-switch-font-color: rgba(255, 255, 255, 0.90);
  --paragraph-font-color: rgba(255, 255, 255, 0.90);
  --lined-heading-color: rgba(255, 255, 255, 0.90);
  --header-border-bottom-color: #222222;
  --header-border-top-color: #222222;
  --anchor-tag-font-color: rgba(255, 255, 255, 0.90);
  --anchor-tag-font-hover-color: #555555;
  --footer-text-color: #eeeeee;
  --terms-font-color: rgba(255, 255, 255, 0.90);
  --nav-link-before-hover-color: rgba(255, 255, 255, 0.90);
  --nav-link-before-color: #222222;
  --hero-tag-color: rgba(255, 255, 255, 0.90);
  --default-heading-color: rgba(255, 255, 255, 0.90);
  --unordered-list-color: rgba(255, 255, 255, 0.90);
  --tags-border-color: #ffffff;
  --pagination-button-default-bg: #ffffff;
  --pagination-button-hover-bg: #222222;
  --pagination-button-default-font-color: #222222;
  --pagination-button-hover-font-color: #ffffff;
  --notfound-frown-face-color: #ffffff;
  --syntax-highlight-bg-color: rgba(0, 0, 0, 0.3);
  --blockquote-background-bg-color: #282c35;
  --blockquote-font-color: #fff;
  --blockquote-border-color: #fff;
}

body {
  background-color: var(--background-color);
  font-family: 'Mulish', sans-serif;
  font-optical-sizing: auto;
  font-size: rem($font-size);
  scroll-behavior: smooth;
}

p {
  color: var(--paragraph-font-color);
  margin-bottom: rem(20px);
}

p,
ul,
pre {
  font-size: rem(14px);
  font-weight: 400;
}

h6 {
  font-size: rem(16px);
}

h5 {
  font-size: rem(18px);
}

h4 {
  font-size: rem(20px);
}

h3 {
  font-size: rem(22px);
}

h2 {
  font-size: rem(24px);
}

h1 {
  font-size: rem(26px);
}

h1,
h2,
h3,
h4,
h5,
h6 {
  color: var(--default-heading-color);
}

ul,
ol {
  color: var(--unordered-list-color);
}

blockquote {
  background-color: var(--blockquote-background-bg-color);
  border-left: rem(4px) solid var(--blockquote-border-color);
  padding: rem(10px);
  margin-top: rem(10px);
  margin-bottom: rem(10px);
  font-size: rem(16px);

  p {
    color: var(--blockquote-font-color);
    margin-bottom: 0;
  }
}

header.navigation {
  padding: 30px 0px;
  border-bottom: rem(1px) solid var(--header-border-bottom-color);
  border-top: rem(6px) solid var(--header-border-top-color);

  nav {
    line-height: rem(45px);
    cursor: pointer;

    a {
      text-transform: capitalize;
      color: var(--nav-link-color);
      margin-right: rem(20px);
      text-decoration: none;
      width: rem(50px);
    }

    a:hover {
      color: var(--nav-link-hover-color);
      text-decoration: none;
    }

    a::before {
      content: "";
      width: rem(8px);
      height: rem(8px);
      border-radius: rem(30px);
      background-color: var(--nav-link-before-color);
      display: inline-block;
      position: relative;
      top: rem(-1px);
      left: rem(-5px);
      transition: all 0.4s;
    }

    a:hover::before {
      background-color: var(--nav-link-before-hover-color);
      transition: all 0.2s;
    }
  }

  .logo {
    background-image: var(--logo);
    background-size: contain;
    background-position: center;
    width: auto;
    height: rem(20px);
    background-repeat: no-repeat;
    position: relative;
    top: rem(15px);
  }
}

figure.avatar {
  position: relative;
  margin: 0 auto;
  width: 160px;
  height: 160px;

  img {
    width: inherit;
    height: inherit;
    margin: 0 auto;
    border-radius: 50%;
    display: block;
    position: absolute;
    opacity: 0;
  }

  img.show {
    opacity: 1;
  }
}

div.profile {
  position: relative;
  margin: 0 rem(180px);
  text-align: center;

  p.description,
  p.contact {
    font-size: rem(13px);
    text-align: center
  }

  h4.title {
    font-weight: 600;
    text-transform: capitalize;
    margin-top: rem(20px);
  }

  margin-bottom: rem(50px);
}

.hero-post {
  .hero-title {
    font-size: rem(30px);
    text-align: center;
    color: var(--post-title-color);
  }

  .hero-post-info {
    text-align: center;
    color: var(--normal-text-color);
    text-transform: uppercase;
    font-size: rem(12px);
    display: flex;
    justify-content: space-between;
    margin: 20px 0px;

    a {
      color: var(--normal-text-color);
      text-decoration: none;
    }

    .date {}

    .read {}
  }

  .hero-image {
    min-height: rem(400px);

    img {
      width: 100%;
      min-height: auto;
      margin-bottom: rem(20px);
    }
  }

  .hero-tags {
    text-align: center;
    margin: rem(60px) 0;

    a.tag {
      border: 2px solid var(--hero-tag-color);
      color: var(--hero-tag-color);
      padding: rem(3px) rem(20px);
      border-radius: rem(50px);
      font-size: rem(13px);
      text-decoration: none;
      margin: rem(10px);
      display: inline-block;
    }

    a.tag:hover {
      transform: scale(1.2);
    }

  }
}

aside.toc {
  position: absolute;
  margin-top: 15px;
  width: rem(200px);
  height: 75%;
  right: rem(-230px);
  color: #222;

  a {
    color: var(--anchor-tag-font-color);
  }

  a:hover {
    color: var(--anchor-tag-font-hover-color);
  }

  .content {
    position: sticky;
    top: rem(100px) !important;

    h2::before {
      content: "";
      position: absolute;
      top: rem(-10px);
      width: rem(30px);
      height: rem(2px);
      background-color: #222;
    }

    ul {
      padding-left: 20px;
      font-size: rem(18px);
    }

    ul:first-child {
      padding-left: 0px;
    }
  }
}

main.app-container {
  min-height: calc(100vh - 250px);
}

main {
  margin-top: rem(30px);

  a {
    color: var(--anchor-tag-font-color);
  }

  a:hover {
    color: var(--anchor-tag-font-hover-color);
  }

  article.post {
    p {
      margin-bottom: rem(15px);
    }
  }

  section.posts {
    article.post {
      margin-bottom: rem(30px);

      .featured-title {
        color: var(--post-featured-color);
        text-transform: uppercase;
        font-weight: 300;
        font-size: rem(11px);
      }

      span.date {
        color: var(--post-date-color);
        font-size: rem(11px);
        text-transform: uppercase;
      }

      .thumbnail {
        width: 100%;
        margin-top: rem(32px);
        background-size: cover;
        background-position: center;
        background-repeat: no-repeat;
        width: 100%;
        height: auto;
      }

      .title {
        a {
          color: var(--post-title-color);
        }
      }

      .tags {
        .tag {
          background-color: #fff;
          color: #222;
          border: 2px solid var(--tags-border-color);
          padding: 3px 15px;
          font-size: rem(10px);
          font-weight: 600;
          display: inline-block;
          margin-right: rem(5px);
          margin-bottom: rem(10px);
          border-radius: rem(50px);
        }

        .tag:hover {
          text-decoration: none;
          border: 2px solid #aaa;
          transform: scale(1.1);
        }
      }
    }
  }
}

.terms {
  .list {
    .tag {
      padding: 5px 10px;
      margin: 10px;
      text-decoration: none;
      color: var(--terms-font-color);
      font-size: rem(12px);
      text-transform: capitalize;
      display: inline-block;
      border-width: rem(2px);
      border-style: solid;
      border-radius: rem(4px);
    }

    .tag:hover {
      transform: scale(1.1);
    }
  }
}

.theme-switch {
  position: fixed;
  top: rem(20px);
  right: rem(50px);
  font-size: rem(22px);
  padding: 20px;
  cursor: pointer;
  color: var(--theme-switch-font-color);
}

nav.pagination {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-top: 20px;
  margin-bottom: 60px;

  a {
    text-align: center;
    font-size: rem(16px);
    background-color: var(--pagination-button-default-bg);
    color: var(--pagination-button-default-font-color);
    border-radius: 50px;
    border: 2px solid #222;
    transition: all 0.4s;
    padding: rem(15px) rem(60px);
    text-decoration: none;
  }

  a:hover {
    background-color: var(--pagination-button-hover-bg);
    color: var(--pagination-button-hover-font-color);
    transition: all 0.4s;
  }

  a.disabled {
    color: #ddd;
    cursor: not-allowed;
  }

  a.disabled:hover {
    background-color: var(--pagination-button-default-bg);
  }
}

.lined-heading {
  font-size: rem(40px);
  margin: rem(40px) 0;
  color: var(--lined-heading-color)
}

.lined-heading::before {
  content: "";
  width: rem(50px);
  height: rem(3px);
  background-color: var(--lined-heading-color);
  display: inline-block;
  position: relative;
  top: rem(-7px);
  left: rem(-15px);
}

.lined-heading::after {
  content: "";
  width: rem(50px);
  height: rem(3px);
  background-color: var(--lined-heading-color);
  display: inline-block;
  position: relative;
  top: rem(-7px);
  right: rem(-15px);
}

section.featured-posts {
  background-color: #fff;
  color: #222;
  display: flex;
  height: auto;

  article.featured-post {
    flex-grow: 1;
    flex-basis: 0;
    text-align: center;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    width: 100%;
    position: relative;

    a {
      color: #fff;
      text-decoration: none;
    }

    img {
      width: 100%;
      height: auto;
      position: relative;
    }

    .overlay-content {
      position: absolute;
      width: 100%;
      height: 100%;
      top: 0;
      padding: rem(30px);
      color: #fff;
      background-color: rgba(0, 0, 0, 0.5);
      transition: all 0.4s;

      h2 {
        font-size: rem(25px);
      }

      .featured-tag {
        display: inline-block;
        padding: rem(15px);
        border: rem(3px) solid #fff;
        color: #fff;
        margin: rem(20px) 0px;
        font-size: rem(18px);
        cursor: default;
        text-decoration: none;
      }
    }

    .overlay-content:hover {
      background-color: rgba(0, 0, 0, 0.7);
      transition: all 0.4s;
    }
  }
}

.telegram_discuss {
  text-decoration: none !important;
  border-radius: rem(50px);
  display: inline-block;
  margin-bottom: rem(20px);
  margin-top: rem(50px);
  position: relative;
  padding: rem(10px) rem(15px) rem(10px) rem(40px);

  &::before {
    content: '';
    background: url(/assets/images/telegram-logo.svg);
    width: rem(25px);
    height: rem(25px);
    position: absolute;
    top: rem(8px);
    left: rem(10px);
    background-size: contain;
    background-repeat: no-repeat;
  }

  border: 2px solid #0088cc;
}

.telegram_discuss:hover {
  transform: scale(1.05);
}

.telegram_discuss.post {
  background-color: #fff;
  color: #222;
}

footer {
  color: var(--footer-text-color);
  font-weight: 400;

  a {
    color: var(--footer-text-color);
  }

  a:hover {
    color: var(--footer-text-color);
  }

  .copyright {
    display: block;
    font-size: rem(13px);
  }

  .copyright::before {
    content: "";
    width: rem(150px);
    height: rem(1px);
    background-color: #fff;
    display: inline-block;
    position: relative;
    top: rem(-5px);
    left: rem(-30px);
  }

  .copyright::after {
    content: "";
    width: rem(150px);
    height: rem(1px);
    background-color: #fff;
    display: inline-block;
    position: relative;
    top: rem(-5px);
    right: rem(-30px);
  }
}

.overlay {
  display: none;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  position: fixed;
  top: 0;
  right: 0;
  left: 0;
  bottom: 0;
}

.alert {
  width: 100%;
  padding: 20px;
  background-color: #eee;
  position: fixed;
  top: 0px;
}

.alert.success {
  background-color: #78e08f;
  color: #fff;
}

.alert.error {
  background-color: #e55039;
  color: #fff;
}

.alert.warning {
  background-color: #f6b93b;
  color: #fff;
}

.alert.other {
  background-color: #82ccdd;
  color: #fff;
}

.error-404-title {
  font-size: rem(50px);
}

.frown-face {
  font-size: rem(70px);
  margin: rem(20px) auto;
  text-align: center;
  color: var(--notfound-frown-face-color)
}

@import 'responsive';