@function rem($pixels){
  @return $pixels / $font_size + rem;
}

.align-center {
  text-align: center;
}

.flex-center {
  justify-content: center !important;
}

.align-right {
  text-align: right;
}

.display-block {
  display: block;
}

.margin-auto {
  margin: auto;
}

.lds-ripple {
  display: block;
  position: relative;
  width: 80px;
  height: 80px;
  margin: auto;
  top: 50%;
  transform: translateY(-50%);
}
.lds-ripple div {
  position: absolute;
  border: 4px solid #fff;
  opacity: 1;
  border-radius: 50%;
  animation: lds-ripple 1s cubic-bezier(0, 0.2, 0.8, 1) infinite;
}
.lds-ripple div:nth-child(2) {
  animation-delay: -0.5s;
}
@keyframes lds-ripple {
  0% {
    top: 36px;
    left: 36px;
    width: 0;
    height: 0;
    opacity: 1;
  }
  100% {
    top: 0px;
    left: 0px;
    width: 72px;
    height: 72px;
    opacity: 0;
  }
}