.highlight {
    pre, code {
        font-family: 'Courier New', Courier, monospace;
        font-size: rem(13px);
        padding: 0 rem(20px);
        border-radius: rem(10px);
        white-space: pre-wrap;
        word-wrap: break-word;
        background-color: transparent;
    }
}

pre, code {
    background-color: var(--syntax-highlight-bg-color);
    font-family: 'Courier New', Courier, monospace;
    font-size: rem(13px);
    padding: 0 rem(10px);
    border-radius: rem(10px);
    white-space: pre-wrap;
    word-wrap: break-word;
}